import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import Back_URL from './validation/BackendUrl'

export default function Dashboard() {
    const [offerList, setOfferList] = useState([]);
    const [shrtList, setShrtList] = useState([]);
    const [offerErr, setOfferErr] = useState(null);
    const [properties, setProperties] = useState([]);
    useEffect(() => {
        let token = localStorage.getItem("token");
        axios.get('api/dashboardofferlist', {
            headers: {
                "x-access-token" : token,
                "Content-Type" : "application/json"
            }
        })
        .then(function (res) {
            setOfferList(res.data.offerList);
            setShrtList(res.data.shortList);
        })
        .catch(function (err) {
            setOfferErr(err.response.data);
        });
        axios.get('api/randomproperties', {
            headers: {
                "x-access-token" : token,
                "Content-Type" : "application/json"
            }
        })
        .then(function (res) {
            setProperties(res.data);
        })
        .catch(function (err) {
            setOfferErr(err.response.data);
        });
    },[]);
    return (
        <>
            {offerErr?<div className='server_error'>
                <span className='form_err'>{offerErr}</span>
            </div>:""}
            <section className="logged spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="inner_boxes">
                                <h3>Offered Properties by you</h3>
                                {offerList.length > 0?<div className='property_lst'>
                                    {offerList.map(list =>
                                        <div key={list.id} className={list.user_upload_status === 'open'?"prop_list_wr":"prop_list_wr disabled"}>
                                            <img src={Back_URL+list.img} className='inline_list_img' alt={'offered property '+offerList.length} />
                                            <span className='prop_location'>{list.city}</span>
                                            {list.user_upload_status === 'open'?<Link to={"/property/"+list.id}>Details</Link>:
                                            <span>Details</span>}
                                        </div>
                                    )}
                                </div>:
                                <div className="property_lst">
                                    <p className="onthing_quote">You have not offered anything yet.</p>
                                    <div className="btn-right"><Link to="/searchresult/1e282121721d3f243b2c392872" className="site-btn">Search</Link></div>
                                </div>}
                                {offerList.length > 1?<div className='text-right'><Link to="/offerred-property" className='readmore-btn'>View All</Link></div>:""}
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="inner_boxes">
                                <h3>Shortlisted Properties</h3>
                                {shrtList.length > 0?<div className='property_lst'>
                                    {shrtList.map(list =>
                                        <div key={list.id} className={list.user_upload_status === 'open'?"prop_list_wr":"prop_list_wr disabled"}>
                                            <img src={Back_URL+list.img} className='inline_list_img' alt={'shortlist property '+shrtList.length} />
                                            <span className='prop_location'>{list.city}</span>
                                            {list.user_upload_status === 'open'?<Link to={"/property/"+list.id}>Details</Link>:
                                            <span>Details</span>}
                                        </div>
                                    )}
                                </div>:
                                <div className="property_lst">
                                    <p className="onthing_quote">All your shortlisted propertes will show here.</p>
                                    <div className="btn-right"><Link to="/searchresult/1e282121721d3f243b2c392872" className="site-btn">Search</Link></div>
                                </div>}
                                {shrtList.length > 1?<div className='text-right'><Link to="/shortlisted-property" className='readmore-btn'>View All</Link></div>:""}
                            </div>
                        </div>
                    </div>
                    <a href="https://www.facebook.com/Jjackeyy/" className='dash_banner' target="_blank" rel="noopener noreferrer">
                        <img src="/img/banner.jpg" alt="Banner" />
                    </a>
                    {properties.length > 0?<><h3 className="prop_list_head">Some property you may interested</h3>
                    <div className='row'>
                        {properties.map((list, index) =>
                            <div key={list.id} className="col-sm-6 col-md-4 col-lg-3">
                                <div className={list.user_upload_status === "open"?"property-item":"property-item disabled"}>
                                    <div className="pi-image">
                                        <img src={Back_URL+list.img} alt={"image "+(index+1)} />
                                        {list.status === "New"?<div className="pi-badge new">New</div>:list.status === "Offer"?<div className="pi-badge offer">Offer</div>:""}
                                    </div>
                                    <h3><i className="fas fa-rupee-sign"></i>{list.price.toLocaleString()}{list.want_to==="rent_out"?<span className='per_month'>/Month</span>:""}</h3>
                                    {list.property_type === "Private"?<h5>{list.bedrooms !== "N/A" && list.bedrooms !== "1"? list.bedrooms+" Bedrooms": list.bedrooms === "1"? list.bedrooms+" Bedroom":""}  {list.house_type}</h5>:
                                    list.property_type === "Commercial"?<h5>{list.patio === "N/A"?list.patio:list.patio+" SF"} {list.house_type}</h5>:
                                    <h5>{list.property_type}</h5>}
                                    {list.property_type === "Private" || list.property_type === "Upcoming Private Project"?
                                    <div className="pi-metas">
                                        {list.bedrooms !== "N/A"?<div className="pi-meta">{list.bedrooms} Bed</div>:""}
                                        {list.bathrooms !== "N/A" && list.bathrooms !== "No"?<div className="pi-meta">{list.bathrooms} Bath</div>:""}
                                        {list.parking_type === "Garage"?<div className="pi-meta">Garage</div>:""}
                                        <div className="pi-meta">{list.property_area+" SF"}</div>
                                    </div>:
                                    <div className="pi-metas"><div className="pi-meta">For {list.hse_for}</div></div>}
                                    {list.user_upload_status === "open"?<Link to={"/property/"+list.id} className="readmore-btn">Find out more</Link>:
                                    <span className="readmore-btn">Find out more</span>}
                                </div>
                            </div>
                        )}
                    </div></>:""}
                    <div className='row'>
                        <div className="col-lg-12">
                            <div className="inner_boxes">
                                <h3>I wan't to Sell/Rent out</h3>
                                <div className="property_lst">
                                    <p className="onthing_quote">You have not posted anything yet.</p>
                                    <div className="btn-right"><Link to="/post-property" className="site-btn">Post now</Link></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="inner_boxes full_inner">
                        <h3>People offered to your property</h3>
                        <p className="onthing_quote">Price offering details will appear as soon as people offered to your properties</p>
                    </div>
                </div>
            </section>
        </>
    )
}
